.cardLaunch{
    background-color: rgb(40, 40, 40);
    display: flex;
    flex-direction: column;
    width: 400px;
    margin: 10px;
    text-align: center;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    transition: 0.5s;
}
.cardLaunch:hover{
    background-color: rgb(80, 80, 80);
}

.image-launch{
    object-fit: cover;
    max-width: 100%;
}
.boxCenter{
    display: flex;
    justify-content: center;
    align-items: center;
}
.titleLuanch{
    width: 100%;
    height: 15%;
    background-color: transparent;
    color: white;
    font-size: 1.2rem;
}
.text{
    margin: 10px 0;
}
.yearLaunch{
    width: 100%;
    height: 10%;
    background-color: transparent;
    color: white;
    font-size: 1.2rem;
}
.resultLaunch{
    width: 100%;
    height: 10%;
    background-color: transparent;
    font-size: 1.1rem;
}
.fail {
    color: rgb(199, 65, 65);
}
.success{
    color:rgb(3, 129, 20)
}