.rocketBackground{
    background-image: url('../images/rocket/bg1.jpg');
    background-size: cover;
    width: 100%;
    height: 100vh;
    position: fixed;
    overflow-y: scroll;
}

.navDummy{
    display: flex;
    width: 100%;
    height: 10vh;
    position: fixed;
    top: 0;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.9);
}

.gradient{
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(
        to bottom,
        rgba(68, 67, 67, 0),
        rgba(0, 0, 0, 0.562)
    );
    position: fixed;
    background-size: cover;
    overflow-y: scroll;
}

.containerDetail{
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    margin-top: 20vh;
    color: white;
    flex: 1;
}

.detail > div > h2, .detailLeftInfo > h2{
    text-transform: uppercase;
    font-size: 24pt;
    font-family: 'Viga', sans-serif;
    margin-top: 0;
}

.detail > div > p, .detailLeftInfo > p{
    font-size: 20px;
    font-family: 'Hind Siliguri', sans-serif;
    margin: 0;
}

.detailLeft{
    display: flex;
    flex-direction: column;
    flex: 2;
}

.detailListRow{
    display: flex;
    flex-direction: row;
}

.detailListRow > .detailList{
    width: 35%;
}

.detailListCol{
    max-width: 80%;
}

.detailRight{
    display: flex;
    flex-direction: column;
    flex: 1;
}

.detailLeftInfo{
    margin-top: 10%;
}

.detailList{
    display: flex;
    flex-direction: column;
    margin-top: 8%;
    margin-right: 5%;
}

.titleDetailList{
    text-align: center;
    margin-bottom: 20px;
}

.titleDetailList > h3{
    font-size: 3vh;
    text-transform: uppercase;
    margin: 0;
}

.detailListInfo{
    display: flex;
    flex-direction: column;
    flex: 1;
}

.detailListInfo > div{
    border-bottom: 0.5px #bbb solid;
    margin-bottom: 20px;
    padding-bottom: 5px;
}

.detailListInfo > div > h3{
    margin: 0;
}

.detailListInfo > div > p:first-child{
    float: left;
    margin: 0;
}

.detailListInfo > div > p:last-child{
    float: right;
    margin: 0;
}

.detailImage{
    display: flex;
    flex-direction: column;
}

.detailImage > div> img{
    width: 100%;
}

#titleDetail{
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
}

#titleDetail > h1{
    text-transform: uppercase;
    font-size: 6vh;
    font-family: 'Viga', sans-serif;
}

#titleDetail, .aboutDetail{
    font-size: 2.6vh;
    font-family: 'Hind Siliguri', sans-serif;
}

#aboutDetail{
    display: flex;
    flex-direction: row;
    flex: 1;
}

#aboutDetail > div > p, .detailListInfo > div > p{
    margin: 0;
}

@media (max-width: 1000px) {
    #titleDetail > h1{
        text-align: center;
    }

    .containerDetail{
        width: 100%;
    }
}

@media (max-width: 550px) {
    #aboutDetail{
        display: flex;
        flex-direction: column-reverse;
        flex: 1;
    }

    .detailLeftInfo, .detailRight > .detailList{
        margin-left: 10%;
        padding-right: 10%;
    }

    #titleDetail{
        padding-left: 10%;
        width: 90%;
    }

    .detailListRow, .detailListCol{
        flex-direction: column;
        max-width: 100%;
    }

    .detailListRow > .detailList{
        width: 100%;
    }

    .detailImage{
        flex-direction: row;
        width: 100vw;
        overflow-x: scroll;
        margin-left: 0;
    }

    .detailImage > div> img{
        width: 90vw;
    }
}